<template>
	<div id="overview" v-title :data-title="$route.query.isSannongLink && $route.query.isSannongLink === '1' ? '绿色田园' : '农业总览'">
		<div id="ov-modules">
			<component
				:is="mapmodule"
				:farmAreaInfo="farmAreaInfo"
				:data-target-flag="dataTargetFlag"
				:groupFlag="groupFlag"
				:showAreaSelecter.sync="showAreaSelecter"
				:delete-aida-land="false"
				@areaChangeEv="areaChangefn"
				ref="mapref"
			></component>
			<template>
				<div class="ov-module" v-for="(module, index) in moduleNameList" :key="module + index">
					<component
						:is="module"
						ref="child"
						:farmAreaInfo="farmAreaInfo"
						:gisMapFlag="gisMapFlag"
						:data-target-flag="dataTargetFlag"
						:groupFlag="groupFlag"
						@handleClick="handleClick"
					></component>
				</div>
			</template>
			<!-- 总览页直接明细列表弹窗 -->
			<keep-alive>
				<div class="detail-dialog" v-if="!!detailDialog.show">
					<layout-box>
						<div class="dialog-container">
							<div class="detail-dialog-title">
								<div class="detail-dialog-title-box">
									<span>{{ detailDialog.title }}</span>
								</div>
							</div>
							<component
								ref="detailDialog"
								:farmAreaInfo="farmAreaInfo"
								:is="detailDialog.show"
								:hideColumn="detailDialog.hideColumn"
								:isHeaderShow="false"
								:isSearchShow="false"
								:isPageShow="false"
								:defaultPageSize="300"
								minHeight="calc(100% - 50px)"
								minWidth="100%"
								height="100%"
							></component>
						</div>
					</layout-box>
					<span class="close-btn" @click="detailDialog.show = ''"></span>
					<div class="detail-btn" @click="goDetail" v-if="detailDialog.showGoDetailBtn">
						<span>{{ detailDialog.goDetailBtnText }}</span>
						<i class="iconfont icon-shuangjiantoushang"></i>
					</div>
				</div>
			</keep-alive>
			<div class="detail-mask" v-if="detailDialog.show"></div>
		</div>
	</div>
</template>
<script>
import {getStorage} from '@/utils/storage';
import {areaLevelCorAreaId} from '@/common/fn';
import destroyVideos from '@/common/fn/destroyVideos';
import areaSource from '@/common/data/shanghai-gis-source.js';
export default {
	data() {
		return {
			gisMapFlag: false, // 是否为gismap
			dataTargetFlag: 0, // 数据源 0神农口袋 1种植系统
			groupFlag: false, // 是否为集团版
			showAreaSelecter: true, // 是否显示区域选择器
			farmAreaInfo: {
				farmAreaType: 1,
				farmAreaId: 0,
				farmAreaName: '',
			},
			moduleList: [], // A
			moduleMap: {
				// 序号-模块名 映射表
				1: 'govRegulatory1', // 1执法监管
				2: 'operatorsTypeNum2', // 2经营主体类型统计
				3: 'operatorstypeArea3', // 3 经营主体占地统计
				4: 'businessScale4', // 4 经营规模统计
				5: 'businessCategory5', // 5 经营类目统计
				6: 'priceTrend6', // 6 价格走势
				7: 'farmProducts7', // 7 在田种类统计
				8: 'landDistributed8', // 8 生产用地类型统计
				9: 'greenCertification9', // 9 绿色认证统计
				10: 'plotVideo10', // 10 视频监控
				11: 'operatorsDynamic11', // 11 生产作业上报
				12: 'argiMaterialsStatistic12', // 12 农资使用统计
				13: 'operatorsNum13', // 13 经营主体统计
				14: 'argiMaterialsTrend14', // 14 农资使用趋势
				15: 'farmAreaNum15', // 15 在田面积统计
				16: 'operators16', // 16 经营主体
				17: 'productionResources17', // 17 生产资源
				18: 'farmAreaNum18', // 18 新的在田地块统计
				19: 'greenCertification19', // 19 绿色认证
				20: 'marketSupervision20', // 20 市场监管
				21: 'businessScaleStatistics21', // 21 经营规模统计
				22: 'score22', // 22 各区直报评分
				23: 'marketSupervision23', // 23 市场监管
			},
			//明细弹框
			detailDialog: {
				show: '',
				title: '',
				showGoDetailBtn: true,
				goDetailBtnText: '查看全部',
				hideColumn: {},
			},
		};
	},
	watch: {},
	computed: {
		moduleNameList() {
			var m = JSON.parse(JSON.stringify(this.moduleList)).splice(0, 10);
			return m.map(index => this.moduleMap[index]);
		},
		mapmodule() {
			return this.gisMapFlag ? 'arcgismap-list' : 'leafletMap'; // gisMap ,leafletMap
		},
	},
	props: [],
	created() {
		var mlist = [12, 1, 3, 11, 4, 6, 5, 10, 7];
		var subjectVo = getStorage('govGovernmentSubjectVO');
		this.gisMapFlag = Boolean(subjectVo.gisMapFlag);
		this.dataTargetFlag = Number(subjectVo.dataTargetFlag);
		this.groupFlag = Boolean(subjectVo.groupFlag);
		if (subjectVo.dashboadTemplateStr && subjectVo.dashboadTemplateStr.split(',').length) {
			mlist = subjectVo.dashboadTemplateStr.split(',');
			mlist.splice(9, 1000); // 旁边最多显示九个模块
		}
		this.moduleList = mlist;
		this.farmAreaInfo = areaLevelCorAreaId();
		// 自定义 显示某个模块
		//this.moduleList = [12, 14, 11, 22];
		// this.gisMapFlag = true;
	},
	async mounted() {
		var That = this;
		function debounce(fn, delay) {
			let timer = null;
			return function () {
				const context = this;
				const args = arguments;
				clearTimeout(timer);
				timer = setTimeout(function () {
					fn.apply(context, args);
				}, delay);
			};
		}
		function resizeHander() {
			if (That.$route.path.indexOf('/home/overview') == -1) return;
			if (!That.$refs.child || !That.$refs.child.length) return;
			That.$refs.child.map(childcomponent => {
				childcomponent.refreshRender && childcomponent.refreshRender();
			});

			if (!That.$refs.detailDialog) return;
			That.$refs.detailDialog.refreshRender && That.$refs.detailDialog.refreshRender();
		}
		window.resizefn = debounce(resizeHander, 200);
		window.removeEventListener('resize', resizefn, false);
		window.addEventListener('resize', resizefn, false);
	},
	activated() {
		// 页面恢复焦点
		this.updateAllModules(true);
		var title = this.$route.query.isSannongLink && this.$route.query.isSannongLink === '1' ? '绿色田园' : '农业总览';
		document.title = title;
		// 解决长时间停留在明细页返回，弹框内table的高度变为0的问题
		if (this.$refs.detailDialog && this.$refs.detailDialog.resizeHandle) {
			this.$refs.detailDialog.resizeHandle();
		}
	},
	deactivated() {
		// 页面失焦
		if (!this.$refs.child) return;
		// 视频模块 和实时动态模块 要禁止渲染 防止不断耗费流量
		this.$refs.child.map(childcomponent => {
			childcomponent.stopRender && childcomponent.stopRender();
		});
	},
	destroyed() {
		window.removeEventListener('resize', resizefn, false);
		window.resizefn = null;
	},
	methods: {
		updateAllModules(onlySome = false) {
			var someNeedUpdateList = ['plot-video-10', 'operators-dynamic-11']; // 其他页面跳转回总览时，需要更新的模块
			// 更新所有模块
			if (!this.$refs.child) return;
			this.$refs.child.map(childcomponent => {
				var childname = childcomponent.$options.name;
				if (!onlySome || someNeedUpdateList.includes(childname)) {
					childcomponent.initMoudle && childcomponent.initMoudle();
				}
			});
			this.$refs.mapref.initMoudle && this.$refs.mapref.initMoudle();
		},
		areaChangefn(obj) {
			this.farmAreaInfo.farmAreaType = obj.farmAreaType;
			this.farmAreaInfo.farmAreaId = obj.farmAreaId;
			this.farmAreaInfo.farmAreaName = obj.farmAreaName;
			this.updateAllModules();
		},
		async handleClick(type, headertiltle) {
			this.detailDialog.show = '';
			this.detailDialog.title = '';
			this.detailDialog.showGoDetailBtn = true;
			this.detailDialog.goDetailBtnText = '查看全部';
			this.detailDialog.hideColumn = {};
			await new Promise(resolve => {
				this.$nextTick(() => {
					resolve();
				});
			});
			switch (type) {
				case 'fieldsStatisticsList':
					this.detailDialog.show = 'fieldsStatisticsList';
					this.detailDialog.title = headertiltle;
					this.detailDialog.hideColumn = {
						handle: true,
					};
					break;
				case 'lawSuperviseList':
					this.detailDialog.show = 'lawSuperviseList';
					this.detailDialog.title = headertiltle;
					this.detailDialog.hideColumn = {
						handle: true,
						patrolType: true,
					};
					break;
				case 'operateSubjectList':
					this.detailDialog.show = 'operateSubjectList';
					this.detailDialog.title = headertiltle;
					this.detailDialog.hideColumn = {
						companyCategoryIdentifier: true,
						companyCategoryName: true,
						farmAcre: true,
						legalPerson: true,
						legalPersonPhone: true,
						handle: true,
					};
					break;
				case 'greenCertList':
					this.detailDialog.show = 'greenCertList';
					this.detailDialog.title = headertiltle;
					this.detailDialog.hideColumn = {
						handle: true,
						prod: true,
					};
					break;
				case 'operators16List':
					this.detailDialog.show = 'operators16List';
					this.detailDialog.title = headertiltle;
					this.detailDialog.showGoDetailBtn = false;
					break;
				case 'production17List':
					this.detailDialog.show = 'production17List';
					this.detailDialog.title = headertiltle;
					this.detailDialog.showGoDetailBtn = false;
					break;
				case 'operatorsDynamicList':
					this.detailDialog.show = 'farmRecordListOther';
					this.detailDialog.title = headertiltle;
					this.detailDialog.showGoDetailBtn = true;
					this.detailDialog.goDetailBtnText = '查看明细';
					break;
			}
		},
		goDetail() {
			this.$router.push({
				name: this.detailDialog.show,
			});
		},
	},
	components: {
		/*
		 overview 子模块须知：
		 methods:
		 initMoudle （必包含） 页面刷新会调用这个方法
		 refreshRender（必包含）  window resize会调用
		 stopRender（可选） 总览页面带缓存 跳转别的页面，会在页面失焦deactivated时 调用此方法 停止网络资源请求，如果当前组件有 实时拉数据功能 必须要有此方法（如直播视频，实时滚动组件）
		 props: {
			 farmAreaInfo: {
				 type: Object,
				 default() {
				    return areaLevelCorAreaId();
				 }
			 }
		 },
		 */
		arcgismapList: () => import('@/components/map/arcgismap-list.vue'), // 中心地图模块 针对上海的arcgis为主的地图框架
		leafletMap: () => import('@/components/overview/map/leaflet-map.vue'), // 中心地图模块 针对全国的普通leaflet为主的地图框架

		govRegulatory1: () => import('@/components/overview/modules/gov-regulatory-1.vue'), // 1执法监管
		operatorsTypeNum2: () => import('@/components/overview/modules/operators-type-num-2.vue'), // 2经营主体类型统计
		operatorstypeArea3: () => import('@/components/overview/modules/operators-type-area-3.vue'), // 3 经营主体占地统计
		businessScale4: () => import('@/components/overview/modules/business-scale-4.vue'), // 4 经营规模统计
		businessCategory5: () => import('@/components/overview/modules/business-category-5.vue'), // 5 经营类目统计
		priceTrend6: () => import('@/components/overview/modules/price-trend-6.vue'), // 6 价格走势
		farmProducts7: () => import('@/components/overview/modules/farm-products-7.vue'), // 7 在田种类统计
		landDistributed8: () => import('@/components/overview/modules/land-distributed-8.vue'), // 8 生产用地类型统计
		greenCertification9: () => import('@/components/overview/modules/green-certification-9.vue'), // 9 绿色认证统计
		plotVideo10: () => import('@/components/overview/modules/plot-video-10.vue'), // 10 视频监控
		operatorsDynamic11: () => import('@/components/overview/modules/operators-dynamic-11.vue'), // 11 生产作业上报
		argiMaterialsStatistic12: () => import('@/components/overview/modules/argi-materials-statistic-12.vue'), // 12 农资使用统计
		operatorsNum13: () => import('@/components/overview/modules/operators-num-13.vue'), // 13 经营主体统计
		argiMaterialsTrend14: () => import('@/components/overview/modules/argi-materials-trend-14.vue'), // 14 农资使用趋势
		farmAreaNum15: () => import('@/components/overview/modules/farm-area-num-15'), // 15 在田面积统计
		operators16: () => import('@/components/overview/modules/operators-16'), // 16 经营主体
		productionResources17: () => import('@/components/overview/modules/production-resources-17'), // 17 生产资源
		farmAreaNum18: () => import('@/components/overview/modules/farm-area-num-18'), // 18 新的 在田地块统计
		greenCertification19: () => import('@/components/overview/modules/green-certification19'), // 19 新的 绿色认证
		marketSupervision20: () => import('@/components/overview/modules/market-supervision-20'), // 20 市场监管
		businessScaleStatistics21: () => import('@/components/overview/modules/business-scale-statistics-21'), // 21 经营规模统计
		score22: () => import('@/components/overview/modules/score-22'), // 22 上海各区评分

		/*明细页*/
		LayoutBox: () => import('@/components/common/LayoutBox'), //明细页 皮肤
		fieldsStatisticsList: () => import('@/pages/overview/fieldsStatisticsList'), //明细页 在田统计
		lawSuperviseList: () => import('@/pages/overview/lawSuperviseList'), //明细页 执法监管
		operateSubjectList: () => import('@/pages/overview/operateSubjectList'), //明细页 经营统计
		greenCertList: () => import('@/pages/overview/greenCertList'), //明细页 经营统计
		operators16List: () => import('@/components/overview/operators16List'),
		production17List: () => import('@/components/overview/production17List'),
		farmRecordListOther: () => import('@/components/overview/operatorsDynamic11List'),
		marketSupervision23: () => import('@/components/overview/modules/market-supervision-23'), // 23 市场监管
	},
};
</script>
<style scoped lang="scss">
@import '~styles/overview/map';
#overview {
	#ov-modules {
		width: 100%;
		height: 100%;
		position: relative;
		overflow: hidden;

		.detail-mask {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			// background-color: #fff;
			z-index: 2;
		}

		.ov-module {
			width: $modulewidth;
			height: $moduleheight;
			//background: rgba(6, 31, 54, 0.75);
			font-size: 30px;
			position: absolute;
			z-index: 10;
			&.map-module {
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
			}
			&:nth-of-type(2) {
				@include moduleTop(0);
				@include moduleLeft(0);
			}
			&:nth-of-type(3) {
				@include moduleTop(1);
				@include moduleLeft(0);
			}
			&:nth-of-type(4) {
				@include moduleTop(2);
				@include moduleLeft(0);
			}
			&:nth-of-type(5) {
				@include moduleTop(2);
				@include moduleLeft(1);
			}
			&:nth-of-type(6) {
				@include moduleTop(2);
				@include moduleLeft(2);
			}
			&:nth-of-type(7) {
				@include moduleTop(2);
				@include moduleLeft(3);
			}
			&:nth-of-type(8) {
				@include moduleTop(2);
				@include moduleLeft(4);
			}
			&:nth-of-type(9) {
				@include moduleTop(1);
				@include moduleLeft(4);
			}
			&:nth-of-type(10) {
				@include moduleTop(0);
				@include moduleLeft(4);
			}
		}
		.detail-dialog {
			width: $modulewidth2;
			height: calc(66% + 2px);
			position: absolute;
			top: 0;
			@include moduleLeft(1);
			z-index: 999;

			// 覆盖表格样式和弹框样式
			/deep/ .layout-box {
				box-shadow: 0 0 13px 0 #00f6ff inset, 0 0 15px 0 #00f6ff;
				background-color: #051f37;

				.fields-statistics,
				.law-supervise,
				.operate-subject,
				.green-cert {
					background-color: #051f37;

					.custom-table.dark /deep/ .el-table {
						thead {
							background-color: #051f37;
						}
						tr.el-table__row:nth-child(odd) {
							background-color: #06233e;
						}
						tr.el-table__row:nth-child(even) {
							background-color: #051f37;
						}
					}

					.loader-light.custom-loader .el-loading-spinner i {
						color: #00e5ff;
					}
					.loader-light.custom-loader .el-loading-spinner .el-loading-text {
						color: #00e5ff;
					}
				}
				// 除绿色认证其他单独设置鼠标状态
				.fields-statistics,
				.law-supervise,
				.operate-subject {
					.custom-table.dark /deep/ .el-table {
						tbody tr {
							cursor: pointer;
						}
						tbody tr.el-table__row:hover {
							background-color: #0a2b4a;
						}
					}
				}
			}

			span.close-btn {
				width: 42px;
				height: 42px;
				z-index: 100;
				background: url('~@/assets/images/pop_close.png') no-repeat center 100%;
				position: absolute;
				top: 12px;
				right: 15px;
				cursor: pointer;
				background-size: 100%;
			}
			.dialog-container {
				width: 99%;
				height: 100%;
				overflow: hidden;
				padding-top: 40px;
				margin: 0 auto;
				box-sizing: border-box;
				background-color: #051f37;

				.detail-dialog-title {
					position: absolute;
					top: -15px;
					display: flex;
					align-items: center;
					justify-content: center;
					height: 50px;
					width: 100%;

					.detail-dialog-title-box {
						width: 398px;
						height: 50px;
						display: flex;
						align-items: center;
						justify-content: center;
						background: url('~@/assets/images/overview/bg-dialog-header.png') no-repeat center 100%;
						font-size: 22px;
						font-family: Microsoft YaHei;
						font-weight: bold;
						color: #00f6ff;
					}
				}
			}
			.detail-btn {
				position: absolute;
				bottom: 1px;
				left: 50%;
				transform: translateX(-50%);
				display: flex;
				align-items: center;
				justify-content: center;
				width: 130px;
				height: 29px;
				background: url('~@/assets/images/overview/bg-more1.png') no-repeat;
				background-size: 100% 100%;
				font-size: 14px;
				color: #02cad4;
				cursor: pointer;
				.iconfont {
					margin: 2px 0 0 4px;
					font-size: 13px;
					color: #098597;
				}
			}
		}
		@media screen and (min-width: 2000px) and (max-width: 3840px) {
			.ov-module {
				width: $modulepwidth;
				height: $modulepheight;
				&.map-module {
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
				}
				&:nth-of-type(3) {
					@include moduleplusTop(0);
					@include moduleplusLeft(0);
				}
				&:nth-of-type(4) {
					@include moduleplusTop(1);
					@include moduleplusLeft(0);
				}
				&:nth-of-type(2) {
					@include moduleplusTop(0);
					@include moduleplusLeft(1);
				}
				&:nth-of-type(5) {
					@include moduleplusTop(1);
					@include moduleplusLeft(1);
				}
				&:nth-of-type(6) {
					width: $modulepwidth2;
					height: calc(30% - 18px);
					top: calc(70% + 18px);
					@include moduleplusLeft(2);
				}
				&:nth-of-type(10) {
					@include moduleplusTop(0);
					@include moduleplusLeft(4);
				}
				&:nth-of-type(9) {
					@include moduleplusTop(1);
					@include moduleplusLeft(4);
				}
				&:nth-of-type(7) {
					@include moduleplusTop(0);
					@include moduleplusLeft(5);
				}
				&:nth-of-type(8) {
					@include moduleplusTop(1);
					@include moduleplusLeft(5);
				}
			}
			.detail-dialog {
				width: $modulepwidth2;
				height: $modulepheight2;
				position: absolute;
				@include moduleplusTop(0);
				@include moduleplusLeft(2);
				span.close-btn {
					width: 40px;
					height: 40px;
					z-index: 9999;
					background: url('~@/assets/images/pop_close.png') no-repeat center 100%;
					position: absolute;
					top: 10px;
					right: 10px;
					cursor: pointer;
					background-size: 100%;
				}
			}
		}
	}

	/*______________________________________________ 总览 地图模块的 覆盖样式 ______________________________________________*/
	/deep/ .map-module {
		.map-control {
			height: $moduleheight2;
			position: absolute;
			@include moduleTop(0);
			@include moduleLeft(1);
			z-index: 3;
		}
		.area-selector {
			position: absolute;
			@include moduleTop(0);
			@include moduleLeft(1);
			z-index: 99;
			.el-select .el-input__inner {
				border-color: #016770 !important;
			}
			.el-select .el-select__caret {
				color: #00f6ff !important;
			}
			.el-input input {
				color: #00f6ff !important;
			}
			.el-input input {
				width: 114px;
				height: 30px;
				line-height: 30px;
				color: #00f6ff;
				font-size: 14px;
				box-shadow: 0 3px 12px -4px rgba(0, 0, 0, 0.6);
				background-color: rgba(5, 30, 52, 0.7);
			}
		}
		.show-green-certland {
			position: absolute;
			@include moduleTop(2);
			@include moduleLeft(4);
			bottom: auto;
			margin-top: -40px;
			margin-left: -520px;
			z-index: 3;
		}
		.gisloader {
			width: $modulewidth2;
			height: calc(66% + 2px);
			position: absolute;
			@include moduleTop(0);
			@include moduleLeft(1);
			z-index: 20;
		}
		.operators-type-list {
			@include moduleTop(0);
			@include moduleRight(1);
		}
		#legendBox {
			@include moduleTop(2);
			@include moduleLeft(1);
			margin-top: -12px;
		}
	}
}
/*当前模块位置部分样式 大屏*/
@media screen and (min-width: 3000px) {
	#overview {
		/deep/ .map-module {
			.map-view {
				width: 100%;
				height: 100%;
			}
			.map-control {
				height: $modulepheight2;
				position: absolute;
				@include moduleplusTop(0);
				@include moduleplusLeft(2);
				z-index: 3;
			}
			.show-green-certland {
				width: 500px;
				position: absolute;
				top: calc(70% + 18px);
				bottom: auto;
				@include moduleplusLeft(4);
				margin-top: -48px;
				margin-left: -520px;
				z-index: 3;
			}
			.gisloader {
				width: $modulepwidth2;
				height: $modulepheight2;
				position: absolute;
				@include moduleplusTop(0);
				@include moduleplusLeft(2);
				z-index: 20;
			}
			.operators-type-list {
				position: absolute;
				@include moduleTop(0);
				@include moduleplusRight(2);
				z-index: 3;
				box-sizing: border-box;
				background: #033453;
				opacity: 0.8;
				transition-property: width, height;
				transition-duration: 0.3s;
			}
			.area-selector {
				position: absolute;
				@include moduleplusTop(0);
				@include moduleplusLeft(2);
			}
			#legendBox {
				top: calc(70% + 18px);
				bottom: auto;
				@include moduleplusLeft(2);
			}
		}
	}
}
</style>



